@import "../global_vars";

.main-arrow-button {
    height: 40px;
    width: 40px;
    cursor: pointer;

    position: fixed;
    top: 30px;
    z-index: 1000;
    &.main-arrow-right {
        right: 30px;
    }
    &.main-arrow-left {
        left: 30px;
    }
}

.main-page {
    padding-top: 6vh;
    background-color: $bg_dark;
    // color: $white;
}



