@import "./FullTimeline";
@import "./Danmaku";

.left-arrow-button {
    height: 40px;
    width: 40px;
    cursor: pointer;

    position: absolute;
    top: 30px;
    right: 30px;
}


.left-page {
    background-color: $bg_dark;
    padding-top: 6vh;
    position: relative;

    .particles {
        z-index: 0;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
    }
}